export const recoverPassword = (password, id, number) => async (dispatch) => {
  dispatch({ type: USER_REGISTER_REQUEST, payload: { password } });
  try {
    const { data } = await Axios.put(
      `${process.env.REACT_APP_API_BASE_URL}/api/users/recoverPassword/${id}/${number}`,
      {
        password,
      }
    );
    // dispatch({ type: USER_REGISTER_SUCCESS, payload: data });
    // dispatch({ type: USER_SIGNIN_SUCCESS, payload: data });
    localStorage.setItem("userInfo", JSON.stringify(data));
  } catch (error) {
    dispatch({
      type: USER_REGISTER_FAIL,
      payload:
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message,
    });
  }
};

export const subscriptionUser =
  (user, subscriptions) => async (dispatch, getState) => {
    dispatch({ type: USER_UPDATE_PROFILE_REQUEST, payload: user });
    const {
      userSignin: { userInfo },
    } = getState();
    try {
      const data = await Axios.post(
        `${process.env.REACT_APP_API_BASE_URL}/api/users/suscribed`,
        {
          user: user,

          subscription: JSON.stringify(subscriptions),
        },
        {
          headers: { authorization: `Bearer ${userInfo.token}` },
        }
      );
      console.log("data del backend.data", data.data);
      dispatch({ type: USER_UPDATE_PROFILE_SUCCESS, payload: data });
      dispatch({ type: USER_SIGNIN_SUCCESS, payload: data.data });
      localStorage.setItem("userInfo", JSON.stringify(data.data));
    } catch (error) {
      const message =
        error.response && error.response.data.message
          ? error.response.data.message
          : error.message;
      dispatch({ type: USER_UPDATE_PROFILE_FAIL, payload: message });
    }
  };
